@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background-color: rgb(61, 61, 65);
}

.App {
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}


#root {
  height: 100vh;
}

.small-screen {
  display: none;
}

@media only screen and (max-width: 960px) {
  .content {
    display: none;
  }

  .small-screen {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    color: white;
    text-align: center;
    margin: 30px 14px;
  }
}
.navbar {
  background-color: #36495d;
  height: 10%;
  display: flex;
  justify-content: start;
  align-items: center;
  /* position: sticky; */
}

.nav-menu {
  width: 18%;
  height: 100vh;
  float: left;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 0px 4px;
  list-style: none;
  min-height: 65px;
}

.centerNavbar {
  justify-content: center;
  align-items: center;
}

.app-title {
  margin-top: 15px;
  color: #f5f5f5;
  font-size: 30px;
  text-decoration: none;
  justify-content: center;
  display: flex;
  align-items: center;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  border-radius: 10px;
  font-size: 14px;
  padding: 10px;
}

.nav-text a:hover {
  background-color: #595a5c;
}
.question {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  vertical-align: top;
  color: rgb(238, 235, 235);
}
.testthegrapth {
  background-color: snow;
}

.aboutpage {
  display: flex;
  justify-content: center;
  align-items: center;
}

.question-title {
  padding: 5%;
  color: #f5f5f5;
}

.hmm {
  overflow-y: scroll;
  color: #f5f5f5;
  font-size: 16px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  padding-top: 2%;
  padding-right: 5%;
  padding-left: 5%;
  text-align: justify;
}
